import React, { Component } from 'react';
import { connect } from "react-redux";
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';

const platformName = (platform) => {
    switch (platform) {
        case 'p-cast':
            return 'Cast'
        case 'p-chromecast':
            return 'Chromecast'
        case 'p-firetvstick':
            return 'Fire Tv Stick'
        case 'p-androidtv':
            return 'Android Tv'
        case 'p-Aptoide':
            return 'Aptoide'
        case 'p-wired':
            return 'Wired'

        case 'p-screen-cloud':
            return 'Screen Cloud';
        case 'p-yodeck':
            return 'Yodeck';
        case 'p-onsign':
            return 'Onsign';
        case 'p-rise-vision':
            return 'Rise Vision';
        case 'p-optisign':
            return 'Optisign';
        case 'p-raydiant':
            return 'Raydiant'
        case 'p-enplug':
            return 'Enplug';
        case 'p-novi-sign':
            return 'Novi Sign';
        case 'dp-other':
            return 'Other';


        case 'p-6connex':
            return '6Connex';
        case 'p-zuddl':
            return 'Zuddl';
        case 'p-gevme':
            return 'Gevme';
        case 'p-vfairs':
            return 'Vfairs';
        case 'p-workcast':
            return 'Workcast';
        case 'p-pathable':
            return 'Pathable';

        case 'p-hubilo':
            return 'Hubilo';
        case 'ev-other':
            return 'Other';
        default:
            return "";
    }
}

class Platforms extends Component {

    render() {
        const { onSelectedPlatform, state, themeSkin } = this.props
        return <>
            <label className="text-gray-800 mb-2">Select Display Platform</label>
            <Accordion.Item eventKey="9" className="pb-3">
                <Accordion.Header className="w-100 d-flex align-items-center">
                    <p className="fs-9 fw-medium mb-0">{`${state.selectedPlatform == 's-projector' ? `Projector / TV` : state.selectedPlatform == 's-signage' ? `Digital Signage` : state.selectedPlatform == 's-event' ? `Virtual / Hybrid Events` : `Select Display Platform`}`}</p>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1">
                    <ToggleButtonGroup type="radio" className="flex-column w-100" name="selectPlatfrom" value={state.selectedPlatform} defaultValue={'s-projector'}>
                        <ToggleButton className={`d-flex align-items-center mb-2 text-gray-800 bg-white`} variant="outline-secondary" id="s_projector" value="s-projector" onClick={onSelectedPlatform('s-projector')}>
                            <i className="ri-tv-2-line icon-5 text-gray-800"></i>
                            <p className="fs-9 fw-medium mb-0">Projector / TV</p>
                        </ToggleButton>
                        <ToggleButton className={`d-flex align-items-center mb-2 text-gray-800 bg-white`} variant="outline-secondary" id="s_signage" value="s-signage" onClick={onSelectedPlatform('s-signage')}>
                            <i className="ri-tablet-line icon-5 text-gray-800"></i>
                            <p className="fs-9 fw-medium mb-0">Digital Signage</p>
                        </ToggleButton>
                        <ToggleButton className={`d-flex align-items-center mb-2 text-gray-800 bg-white`} variant="outline-secondary" id="s_event" value="s-event" onClick={onSelectedPlatform('s-event')}>
                            <i className="ri-picture-in-picture-line icon-5 text-gray-800"></i>
                            <p className="fs-9 fw-medium mb-0">Virtual / Hybrid Events</p>
                        </ToggleButton>
                    </ToggleButtonGroup>
                </Accordion.Body>
            </Accordion.Item>
            {state.selectedPlatform == 's-projector' ? <Accordion.Item eventKey="10" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-9 fw-medium">{platformName(state.showGenratedCode)}</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1">
                    <ToggleButtonGroup type="radio" className="flex-column w-100 btn-group-outline" name="display-platform" defaultValue={'p-cast'}>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-cast" value={'p-cast'} onClick={e => this.props.embedCodeCallback('p-cast')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/cast${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="cast" height={26} width={80} />
                            </div>
                        </ToggleButton>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-chromecast" value={'p-chromecast'} onClick={e => this.props.embedCodeCallback('p-chromecast')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/chromecast${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="chromecast" height={44} width={100} />
                            </div>
                        </ToggleButton>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-firetvstick" value={'p-firetvstick'} onClick={e => this.props.embedCodeCallback('p-firetvstick')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/firetvstick${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="firetvstick" height={30} width={80} />
                            </div>
                        </ToggleButton>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-androidtv" value={'p-androidtv'} onClick={e => this.props.embedCodeCallback('p-androidtv')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/androidtv${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="androidtv" height={45} width={85} />
                            </div>
                        </ToggleButton>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-Aptoide" value={'p-Aptoide'} onClick={e => this.props.embedCodeCallback('p-Aptoide')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/Aptoide-tv${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="Aptoide" height={32} width={110} />
                            </div>
                        </ToggleButton>
                        <ToggleButton className="mb-2" variant="outline-secondary" id="p-wired" value={'p-wired'} onClick={e => this.props.embedCodeCallback('p-wired')}>
                            <div className="block-center text-center h-46px w-100">
                                <img className="img-fluid" src={baseUrl(`media/images/display-platform/wired${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="wired" height={30} width={86} />
                            </div>
                        </ToggleButton>
                    </ToggleButtonGroup>
                </Accordion.Body>
            </Accordion.Item> :
                state.selectedPlatform == 's-signage' ? <Accordion.Item eventKey="11" className="pb-3">
                    <Accordion.Header className="w-100">
                        <span className="fs-9 fw-medium">{platformName(state.showGenratedCode)}</span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <ToggleButtonGroup type="radio" className="flex-column w-100" name="digital-signage" defaultValue={'p-screen-cloud'}>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-screen-cloud" value={'p-screen-cloud'} onClick={e => this.props.embedCodeCallback('p-screen-cloud')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/screen-cloud${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="screen-cloud" height={26} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-yodeck" value={'p-yodeck'} onClick={e => this.props.embedCodeCallback('p-yodeck')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/yodeck${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="yodeck" height={44} width={100} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-onsign" value={'p-onsign'} onClick={e => this.props.embedCodeCallback('p-onsign')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/onsign-tv${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="onsign" height={30} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-rise-vision" value={'p-rise-vision'} onClick={e => this.props.embedCodeCallback('p-rise-vision')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/rise-vision${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="rise-vision" height={40} width={75} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-optisign" value={'p-optisign'} onClick={e => this.props.embedCodeCallback('p-optisign')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/optisign${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="optisign" height={32} width={110} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-raydiant" value={'p-raydiant'} onClick={e => this.props.embedCodeCallback('p-raydiant')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/raydiant${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="raydiant" height={30} width={80} />
                                </div>
                            </ToggleButton>
                            <label className="text-gray-800 mb-2">Other Platforms:</label>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-enplug" value={'p-enplug'} onClick={e => this.props.embedCodeCallback('p-enplug')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/enplug${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="enplug" height={30} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-novi-sign" value={'p-novi-sign'} onClick={e => this.props.embedCodeCallback('p-novi-sign')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/novi-sign${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="novi-sign" height={26} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="dp-other" value={'dp-other'} onClick={e => this.props.embedCodeCallback('p-other')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/other${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="other" height={26} width={70} />
                                </div>
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </Accordion.Body>
                </Accordion.Item> : state.selectedPlatform == 's-event' ? <Accordion.Item eventKey="12" className="pb-3">
                    <Accordion.Header className="w-100">
                        <span className="fs-9 fw-medium">{platformName(state.showGenratedCode)}</span>
                    </Accordion.Header>
                    <Accordion.Body className="border rounded-1 mt-1">
                        <ToggleButtonGroup type="radio" className="flex-column w-100" name="digital-signage" defaultValue={'p-6connex'}>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-6connex" value={'p-6connex'} onClick={e => this.props.embedCodeCallback('p-6connex')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/6connex${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="6connex" height={26} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-zuddl" value={'p-zuddl'} onClick={e => this.props.embedCodeCallback('p-zuddl')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/zuddl${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="zuddl" height={44} width={100} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-gevme" value={'p-gevme'} onClick={e => this.props.embedCodeCallback('p-gevme')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/gevme${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="gevme" height={30} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-vfairs" value={'p-vfairs'} onClick={e => this.props.embedCodeCallback('p-vfairs')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/vfairs${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="vfairs" height={40} width={75} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-workcast" value={'p-workcast'} onClick={e => this.props.embedCodeCallback('p-workcast')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/workcast${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="workcast" height={32} width={110} />
                                </div>
                            </ToggleButton>
                            <label className="text-gray-800 mb-2">Other Platforms:</label>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-pathable" value={'p-pathable'} onClick={e => this.props.embedCodeCallback('p-pathable')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/pathable${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="pathable" height={30} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="p-hubilo" value={'p-hubilo'} onClick={e => this.props.embedCodeCallback('p-hubilo')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/hubilo${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="hubilo" height={26} width={80} />
                                </div>
                            </ToggleButton>
                            <ToggleButton className="mb-2" variant="outline-secondary" id="ev-other" value={'ev-other'} onClick={e => this.props.embedCodeCallback('ev-other')}>
                                <div className="block-center text-center h-46px w-100">
                                    <img className="img-fluid" src={baseUrl(`media/images/display-platform/other${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="other" height={26} width={70} />
                                </div>
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </Accordion.Body>
                </Accordion.Item> : null}

        </>
    }
}

const mapStateToProps = (state) => {
    return {
        themeSkin: state.themeSkin.themeSkin
    }
}
export default connect(mapStateToProps)(Platforms);